.form {
    display: flex;
    flex-direction: column;
    max-width: 400px;
    margin: 0 auto;
}

.form input {
    padding: 8px 15px;
    margin-bottom: 1.5em;
    border-radius: 0;
    border: 1px solid #282c34;
}

.form input[type="subimit"] {
    background-color: rgb(245, 210, 232);
    border: 1px solid rgb(245, 210, 232);
    color: #000;
    transition:  .5s;
    border: none;
}

.form input[type="submit"]:hover {
    background-color: rgb(92, 48, 92);
    color: #fff;
    border-color: #282c34;
    cursor: pointer;
}

.input_container {
    display: flex;
    flex-direction: column;
    text-align: left;
}

.input_container label {
    font-weight: bold;
    margin-bottom: .4em;
}